
import { initCarousel } from "../objects/carousel";
import { setupAddToCart } from "../objects/related-add-to-cart";
import { getElem, getElems } from "../tools/utilities/helper-functions";

const mainContainer = getElem('[data-before-and-after-slider]');

if(mainContainer)(() => {

  const slidesContainer = getElem('[data-slides-container]', mainContainer)!

  const {slider} = initCarousel({
    container: slidesContainer, 
    options: {
      slidesPerView: 1,
      loop: true,
      spacing: 30
    }
  })!;

  getElems('[data-prev-arrow]').forEach(button => 
    button.onclick = slider.prev
  );

  getElems('[data-next-arrow]').forEach(button => 
    button.onclick = slider.next
  );


  setupAddToCart(mainContainer)


  



})()

document.createElement('my-component')

